@import url('https://fonts.googleapis.com/css2?family=VT323&display=swap');

.App {
  text-align: center;  
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(18px + 2vmin);
  color: white;
  @media(max-width: 768px){
    justify-content: flex-start;
  }


  .pokedex {
    display: flex;
    border: 4px solid black;
    border-radius: 55px 10px 10px 10px;
    padding: 0 15px;
    background-color: red;
    width: 800px;
    height: 600px;

    @media(max-width: 768px){
        height: 100%;
        min-height: 100vh;
        width: auto;
        
        flex-direction: column;
        padding: 0;
    }

    .left-page {
        flex: 1;
        display: flex;
        flex-direction: column;
        border-right: 4px solid black;
        padding-bottom: 15px;
        @media(max-width: 768px){
            padding-bottom: 0;
            border-right: 0;
        }

        .left-panel--top-decoration {
            border-top: 0;
            height: 100px;
            display: flex;
            align-items: center;
            @media(max-width: 768px){
              padding-left: 15px;
            }

            .blue-light {
                height: 50px;
                width: 50px;
                border-radius: 35px;
                background-color: #2A84C0;
                border: 8px solid white;
            }
            .small-button {
                align-self:baseline;
                height: 15px;
                width: 15px;
                border-radius: 35px;
                border: 4px solid black;
                margin-top: 10px;
                margin-left: 15px;
            }
            .red-light {
                background-color: red;
            }
            .yellow-light {
                background-color: yellow;
            }
            .green-light {
                background-color: green;
            }
        }
        .left-panel--bottom-decoration {
            height: 25px;
            display: flex;
            padding-left: 50px;
            align-items: center;
          
            .bar {
                width: 75px;
                height: 5px;
                border: 2px solid black;
                border-radius: 25px;
                display: inline-block;
                margin-right: 15px;
            }
            .blue-bar {
                background-color: #2A84C0;
            }
        }
    }
    
    .right-page {
        flex: 1;
        padding-top: 35px;
        padding-left: 15px;   
        @media(max-width: 768px){
            padding-top: 15px;
            padding-right: 15px;
        }
    }
  }
}
