@import url(https://fonts.googleapis.com/css2?family=VT323&display=swap);
@import url(https://fonts.googleapis.com/css2?family=VT323&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;-ms-scroll-chaining:none;overscroll-behavior:none}::-webkit-scrollbar{-webkit-appearance:none;width:7px}::-webkit-scrollbar-corner{background:rgba(0,0,0,0)}::-webkit-scrollbar-thumb{border-radius:4px;background-color:rgba(0,0,0,0.5);box-shadow:0 0 1px rgba(255,255,255,0.5)}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.data-entry{display:flex;padding:10px 15px}@media (max-width: 768px){.data-entry{padding-top:0}}.data-entry .pokedex-input{font-family:'VT323', monospace;height:48px;font-size:36px;border-radius:25px;padding-left:15px;margin-right:5px}@media (max-width: 768px){.data-entry .pokedex-input{width:80%;font-size:30px}}.data-entry .pokedex-input:focus-visible{outline:none}.data-entry button{background-color:#FFC252;height:54px;width:54px;border-radius:48px;border:4px solid black;cursor:pointer}.data-entry button:hover{background-color:#ffd17d}

.history-title{display:flex;justify-content:center;font-family:'VT323', monospace;font-size:28px;color:#000}@media (max-width: 768px){.history-title{font-size:26px}}.history-box{display:flex;padding:5px 15px;flex-direction:column;text-align:left;font-size:16px;height:150px;overflow:scroll}@media (max-width: 768px){.history-box{max-height:600px}}.history-box .no-history{font-family:'VT323', monospace;text-align:center}@media (max-width: 768px){.history-box .no-history{font-size:24px}}.history-box button{text-transform:capitalize;font-family:'VT323', monospace;padding:5px;background-color:#43A14E;border-radius:5px;color:white;font-size:24px;cursor:pointer}.history-box button:hover{background-color:#193d1d}.history-box li{list-style:none;padding:5px 5px 5px 1  5px}

.display{display:flex;flex:1 1;flex-direction:column;text-align:left;border:4px solid black;padding:5px 15px 5px 15px;margin:0 15px;background:#D6D6D6;color:#000;border-radius:5px 5px 5px 25px;font-family:'VT323', monospace}.display .top-decorations{display:flex;justify-content:center;padding-bottom:5px}.display .top-decorations .small-button{height:10px;width:10px;border-radius:35px;border:2px solid black;margin:0 10px}.display .top-decorations .red-light{background-color:red}.display .display-inner{display:flex;flex:1 1;flex-direction:column;text-align:left;border:4px solid black;padding:15px;background:#D6D6C0;color:#000;border-radius:25px;overflow:hidden}.display .display-inner .poke-name{font-size:48px;text-transform:capitalize}.display .display-inner .poke-type{font-size:16px}@media (max-width: 768px){.display .display-inner .poke-type{font-size:24px}}.display .display-inner img{max-height:125px;object-fit:contain}@media (max-width: 768px){.display .display-inner img{max-height:175px}}.display .display-inner .panels{display:flex;flex-direction:row;font-size:16px}@media (max-width: 768px){.display .display-inner .panels{flex-direction:column;font-size:24px}}.display .display-inner .panels .left-panel{flex:1 1;text-transform:capitalize}.display .display-inner .panels .right-panel{flex:1 1}.display .display-inner .panels .right-panel .move-box{height:150px;overflow:scroll;text-align:left}.display .bottom-decorations{display:flex;justify-content:space-between;padding:0 15px}.display .bottom-decorations .small-button{height:25px;width:25px;border-radius:35px;border:4px solid black;margin-top:5px}.display .bottom-decorations .red-light{background-color:red}.display .bottom-decorations .voicebars{height:20px;width:35px}.display .bottom-decorations .voicebars .line{display:flex;height:4px;width:100%;background-color:black;margin:5px 0}

.secondary-display{height:250px;display:flex;flex:1 1;flex-direction:column;text-align:left;border:4px solid black;padding:15px;background:#D6D6D6;color:#000;border-radius:15px;font-family:'VT323', monospace}@media (max-width: 768px){.secondary-display{height:auto;max-height:350px}}.secondary-display .secondary-display--inner{border:4px solid black;background:#D6D6C0;border-radius:5px;height:100%;padding:0 15px;overflow:hidden}.secondary-display .secondary-display--inner .title{text-align:center}.secondary-display .secondary-display--inner .evolution-chain{text-align:center;display:flex;font-size:18px}@media (max-width: 768px){.secondary-display .secondary-display--inner .evolution-chain{padding-bottom:10px;font-size:20px;width:100%;overflow-x:scroll}}.secondary-display .secondary-display--inner .evolution-chain .pkmn{display:flex;flex:1 1;flex-direction:column;cursor:pointer;border:2px solid #D6D6D6;border-radius:10px}.secondary-display .secondary-display--inner .evolution-chain .pkmn:hover{border:2px solid black}.secondary-display .secondary-display--inner .evolution-chain .pkmn img{height:100px;object-fit:contain}@media (max-width: 768px){.secondary-display .secondary-display--inner .evolution-chain .pkmn img{height:75px}}.locations{font-size:18px}@media (max-width: 768px){.locations{font-size:24px}}.locations .pokemon-name{text-transform:capitalize}.locations .location-list{display:flex;max-height:75px;overflow:scroll;flex-direction:column;text-transform:capitalize;list-style-type:none;padding-left:15px}@media (max-width: 768px){.locations .location-list{height:auto;max-height:175px}}.screen-decoration{display:flex;justify-content:flex-end;padding-top:10px}.screen-decoration .bar{background-color:#484646;width:75px;height:5px;border:2px solid black;display:inline-block;margin-right:15px}

.App{text-align:center;background-color:#282c34;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(18px + 2vmin);color:white}@media (max-width: 768px){.App{justify-content:flex-start}}.App .pokedex{display:flex;border:4px solid black;border-radius:55px 10px 10px 10px;padding:0 15px;background-color:red;width:800px;height:600px}@media (max-width: 768px){.App .pokedex{height:100%;min-height:100vh;width:auto;flex-direction:column;padding:0}}.App .pokedex .left-page{flex:1 1;display:flex;flex-direction:column;border-right:4px solid black;padding-bottom:15px}@media (max-width: 768px){.App .pokedex .left-page{padding-bottom:0;border-right:0}}.App .pokedex .left-page .left-panel--top-decoration{border-top:0;height:100px;display:flex;align-items:center}@media (max-width: 768px){.App .pokedex .left-page .left-panel--top-decoration{padding-left:15px}}.App .pokedex .left-page .left-panel--top-decoration .blue-light{height:50px;width:50px;border-radius:35px;background-color:#2A84C0;border:8px solid white}.App .pokedex .left-page .left-panel--top-decoration .small-button{align-self:baseline;height:15px;width:15px;border-radius:35px;border:4px solid black;margin-top:10px;margin-left:15px}.App .pokedex .left-page .left-panel--top-decoration .red-light{background-color:red}.App .pokedex .left-page .left-panel--top-decoration .yellow-light{background-color:yellow}.App .pokedex .left-page .left-panel--top-decoration .green-light{background-color:green}.App .pokedex .left-page .left-panel--bottom-decoration{height:25px;display:flex;padding-left:50px;align-items:center}.App .pokedex .left-page .left-panel--bottom-decoration .bar{width:75px;height:5px;border:2px solid black;border-radius:25px;display:inline-block;margin-right:15px}.App .pokedex .left-page .left-panel--bottom-decoration .blue-bar{background-color:#2A84C0}.App .pokedex .right-page{flex:1 1;padding-top:35px;padding-left:15px}@media (max-width: 768px){.App .pokedex .right-page{padding-top:15px;padding-right:15px}}

